<template>
  <div class="player" :style="mainStyle">
    <a v-if="!isReview" class="goback-nav"
       :href="'mainpage.html?ADTAG='+isqd">
      <img src="../../assets/images/back.png" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播">
      <span>低调看直播</span>
    </a>
    <a
      v-else
      class="goback-nav"
       :href="'replayer.html?ADTAG='+isqd+'&matchId='+matchId+(isWorldCup?'&type=worldCup':'')">
      <img src="../../assets/images/back.png" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播">
      <span>低调看直播</span>
    </a>

    <contact :Banner="bannerArr.contact"></contact>
    <timeScore :IsBlack="true" v-if="!isChannel"></timeScore>
    <div class="mobile" >
      <div class="player-box">
        <div v-show="playType === 1" id="a1" class="player-wrap" ></div>
        <div v-if="playType === 2" class="iframe-box">
          <iframe :src="videosrc" frameborder="0" class="player-wrap"></iframe>
        </div>
        <div class="no-line" v-if="mobileList.length==0 && (!isChannel && !isReview)">
          <span>等待线路更新中</span>
        </div>
      </div>
      <a v-if="mobileList.length>0 && (!isChannel && !isReview)" class="errBox" @click="upErr('click')">
        {{errText}}
      </a>
      <a v-if="isChannel||isReview" class="errBox" @click="channelErr('click')">遇到问题，联系客服微信:993760177</a>

      <div class="line-box" v-if="(!isChannel && !isReview)">
        <div class="line-title">切换线路 <span class="title-tips">(下滑切换更多线路)</span></div>
        <transition name="showline">
        <div class="line-content">
          <div
            v-for="(item,index) in mobileList"
            :class="{'active':item.active,'line-item':true}"
            @click="changeLine(item,index)">
            {{item.name}} <span v-if="item.tag">- {{item.tag}}</span>
          </div>
          <div
            v-if="bannerArr.lineBan.show"
            @click="lastLine()"
            class="line-item">
            <a :href="bannerArr.lineBan.url"
               target="_blank"
               style="display: block;width: 100%;height: 100%;position: absolute;top:0;left: 0;"></a>
            {{bannerArr.lineBan.content}}
          </div>
        </div>
        </transition>
      </div>

      <div class="line-box" v-if="(isChannel && !isReview)">
        <div class="line-title">切换频道 <span class="title-tips">(下滑切换更多频道)</span></div>
        <transition name="showline">
          <div class="line-content">
            <div
              v-for="(item,index) in channelList"
              :class="{'active':item.active,'line-item':true}"
              @click="changeChannel(item,index)">
              {{item.channelName}}
            </div>
            <div
              v-if="bannerArr.lineBan.show"
              @click="lastLine()"
              class="line-item">
              <a :href="bannerArr.lineBan.url"
                 target="_blank"
                 style="display: block;width: 100%;height: 100%;position: absolute;top:0;left: 0;"></a>
              {{bannerArr.lineBan.content}}
            </div>
          </div>
        </transition>
      </div>

      <!--<match-list :MatchType="matchType"></match-list>-->
    </div>
    <transition name="showline">
    <tips :Tips="tipsText" v-if="showtips"></tips>
    </transition>
    <downlink :Banner="bannerArr.downlink"></downlink>
  </div>
</template>

<style lang="less" >
  @import "css/player.less";
  .showline-enter-active, .showline-leave-active {
    /*transform: translateY(0);*/
    opacity: 1;
    transition: all .5s;
  }
  .showline-enter, .showline-leave-to /* .showline-leave-active in below version 2.1.8 */ {
    /*transform: translateY(-100%);*/
    opacity: 0;
  }
  .line-content .line-item.active{
    box-sizing: border-box;
    background: #3b3b3b;
    box-shadow:1px 0px .05rem #999 inset;
  }
</style>
<script type='text/ecmascript-6'>
  import * as utils from '../../common/js/utils'
  import matchList from '../components/matchList/matchList'
  import tips from '../components/tips/tips.vue'
  import Vue from 'vue'
  import downlink from '../components/downlink.vue'
  import contact from '../components/contact.vue'
  import timeScore from '../components/timeScore.vue'

  export default{
    name: 'matchplayer',
    components: {matchList, tips, downlink, contact,timeScore},
    // 正式
    data() {
      return {
        playType: parseInt(utils.getPara('playType')),
        videoType: utils.getPara('videoType'),
        videosrc: utils.getPara('src'),
        matchTitle:utils.getPara('title'),

        isChannel:false,
        channelList:[],
        matchId:utils.getPara('matchId'),
        mobileList:[],

        errText:'无法观看比赛？点我报错或+V993760177',
        mCurrentLine:'',
        lineId:'',
        firstType:'',
        tipsText:'',
        showtips:false,
        matchType:1000,

        mainStyle:{
          paddingBottom: '1.13rem'
        },
        isqd:'',
        isReview:false,
        isWorldCup:false,

        bannerArr:{
          downlink:{iosShow:false,androidShow:false},
          contact:{iosShow:false,androidShow:false},
          appPop:{iosShow:false,androidShow:false},
          listBan:{iosShow:false,androidShow:false},
          lineBan:{iosShow:false,androidShow:false},
        },
      }
    },
    created(){
      this.isqd = utils.getPara('ADTAG')?utils.getPara('ADTAG'):'';
      document.getElementById('loading').style.display = 'none';
      this.isReview = (utils.getPara('matchId')&&(utils.getPara('isChannel')==2||utils.getPara('isChannel')==3)?true:false)
      if(utils.getPara('isChannel')==3){
        this.isWorldCup = true
      }
      this.getBanner()

    },
    mounted() {
      // 1、频道页面进来 2、回放页面进来 3、世界杯页面进来
      if(utils.getPara('isChannel')==1){
        this.getChannelList();
      }
      if(utils.getPara('isChannel')==1 || utils.getPara('isChannel')==2 || utils.getPara('isChannel')==3 ){
        this.isChannel = true
        setTimeout(()=>{
          this.initVideo();
        },500);
      } else{
        /*手机上播1、2php的，电脑上播0、1的*/
        this.isChannel = false
        this.initData();
      }
      /*if (this.playType === 2) {return;} else {}*/
    },
    methods: {
      initVideo(){
        const vthis = this;
        window.videoObject = {
          container: '#a1', //容器的ID或className
          variable: 'player', //播放函数名称,
          live:true,
          autoplay: true, //是否自动播放
          debug:false,
          flashplayer: true, //强制使用flashplayer
          video:'',
          loaded: 'loadedHandler', //当播放器加载后执行的函数
        }
        /*console.log(this.playType,this.videosrc)*/
        if(this.isReview&&this.playType==1){ //回放
          window.videoObject.video = utils.getPara('src');
        }else{
          window.videoObject.video = this.videosrc;
        }
        window.player = new ckplayer(window.videoObject);
        window.loadedHandler = function(){
          console.log('播放器加载完毕')
          window.player.addListener('error',errorHandler);
          function errorHandler () { // 移动端
            console.error('视频加载错误，停止执行其它动作，等待其它操作')
            /*if(utils.getPara('isChannel')==1){
              vthis.channelErr();
            }else{
              vthis.upErr();
            }*/
          }

        }
      },

      initData(){
        let toData = {
          url: 'channel/get',
          postData: {
            matchId: utils.getPara('matchId')
          },
          callback: (res) => {
            /*console.log(res)*/
            if (res.error_code === 1) {
              /*console.table(res.data);*/
              if(JSON.stringify(res.data)=='{}'){
                console.warn('暂时还没有播放线路,请观看其他的线路');
                this.errText = '等待线路更新中'
                return;
              }else{
                let list = res.data;
                for(let i in list){
                  list[i].ative = false
                  if(list[i].source==1||list[i].source==2){ // 移动端 source = 1, 2php
                    this.mobileList.push(list[i])
                  }
                }

                if(this.mobileList.length==0){
                  console.warn('移动端还没有播放线路，请观看其他的线路');
                  this.errText = '等待线路更新中'
                  return
                } else {
                  this.mobileList.map((item,index)=>{
                    if(utils.getPara('currentId')){
                      if(item.id==utils.getPara('currentId')){
                        item.active = true;
                        this.inintLine(index)
                      }
                    }else{
                      if(index==0){
                        item.active = true
                        this.inintLine(index)
                      }
                    }
                  })
                  // record change line count
                  let oldObj = JSON.parse(sessionStorage.getItem('oldObj'))
                  if(!oldObj){
                    let firstObj = {}
                    firstObj.lastLine = this.lineId;
                    sessionStorage.setItem('oldObj',JSON.stringify(firstObj))
                  }
                  if(!sessionStorage.getItem('isFirst')){
                    console.log('初始LineId',this.lineId);
                    sessionStorage.setItem('isFirst',false);
                    if(this.playType==3){
                      window.location.href = 'download.html'
                    }else{
                      let toData = {
                        url:'channel/switch',
                        postData:{nowLine:this.lineId},
                        callback: (res)=>{
                          console.log('初始线路统计',res.data)
                        }
                      }
                      utils.cpost(this, toData);
                    }
                  }

                  this.initVideo();
                }
              }
            }
          },
          error: () => {
            console.error('网络错误')
          }
        }
        utils.cpost(this, toData);
      },

      getBanner(){
        let toData = {
          url:'ggw/mobile/getAll',
          callback:(res)=>{
            /*console.log(res.data)*/
            if(res.error_code==1){
              let list = res.data
              //  type广告位置类型 0：底层广告 1：列表广告 2：悬浮窗广告 3：文字广告
              list.map((item,index)=>{
                switch (item.type){
                  case 0:
                    this.bannerArr.downlink = item
                    break;
                  case 1:
                    this.bannerArr.listBan = item
                    break;
                  case 2:
                    this.bannerArr.appPop = item
                  case 3:
                    this.bannerArr.contact = item
                    break;
                  case 4:
                    this.bannerArr.lineBan = item
                }
              })
              let bower = utils.checkDevice()
              if(bower.versions.android){
                this.bannerArr.lineBan.show = this.bannerArr.lineBan.androidShow
                this.bannerArr.lineBan.url = this.bannerArr.lineBan.androidUrl
              }else{
                this.bannerArr.lineBan.show = this.bannerArr.lineBan.iosShow
                this.bannerArr.lineBan.url = this.bannerArr.lineBan.iosUrl
              }
            }
          }
        }
        utils.cpost(this,toData)
      },

      inintLine(index){
        this.mCurrentLine = this.mobileList[index].name +(this.mobileList[index].tag?' - '+this.mobileList[index].tag:'');
        let mfirstObj = this.mobileList[index]
        this.playType = mfirstObj.playType
        this.videoType = mfirstObj.videoType
        this.videosrc = mfirstObj.url
        this.lineId = mfirstObj.id
        this.firstType = (mfirstObj.playType==1?'video':'iframe')
      },

      changeLine(item,index){
        this.mobileList.forEach(function (item) {
          Vue.set(item,'active',false)
        });
        Vue.set(item,'active',true);

        /*线路统计*/
        let lastObj = JSON.parse(sessionStorage.getItem('oldObj'))
        console.log('上一个id',lastObj.lastLine)
        console.log('当前id',item.id)

        let params = {
          lastLine:lastObj.lastLine,
          nowLine:item.id,
        }
        console.table(params)
        if(params.nowLine==params.lastLine){
          window.location.href = 'player.html?ADTAG='+this.isqd+'&currentId='+item.id+'&matchId='+item.matchId+'&title='+(this.matchTitle?this.matchTitle:'体育直播')
        }else{
          let toData = {
            url:'channel/switch',
            postData:params,
            callback: (res)=>{
              let oldObj = {lastLine:item.id}
              sessionStorage.setItem('oldObj',JSON.stringify(oldObj));
              window.location.href = 'player.html?ADTAG='+this.isqd+'&currentId='+item.id+'&matchId='+item.matchId+'&title='+(this.matchTitle?this.matchTitle:'体育直播')
            }
          }

          if(item.playType==3){
            window.location.href = 'download.html'
          }else{
            utils.cpost(this, toData);
          }

        }
        /*this.mobileList.forEach(function (item) {
          Vue.set(item,'active',false)
        });
        Vue.set(item,'active',true);

        this.lineId = item.id;
        this.mCurrentLine = item.name +' '+(item.tag?item.tag:'')
        this.playType = item.playType
        this.videosrc = item.url;
        console.log(this.firstType )
        if(item.playType==2){//ifarme
        }else{
          if(this.firstType == 'iframe'){
            this.initVideo();
            this.firstType=false;
          }else{
            if(item.source==2){//php
              window.player.newVideo({autoplay:true,live:true,video:'website:'+this.videosrc})
            }else{
              window.player.newVideo({autoplay:true,live:true,video:this.videosrc})
            }
          }

        }*/
      },

      lastLine(){
        MtaH5.clickStat("linebanner")
      },

      /*比赛视频报错*/
      upErr(isclick){
      	/*console.log(isclick)*/
        let toData = {
          url:'alert/insert',
          postData:{
            matchId:this.matchId,
            lineId:this.lineId
          },
          callback:(res)=>{
            if(res.error_code==1){
              console.info('错误上报成功！')
              if(isclick){
                this.showtips = true;
                this.tipsText = '上报成功，正在修复中';
                setTimeout(()=>{
                  this.showtips = false;
                },2000)
              }

            }
          },
          error:()=>{
            console.log('网络错误');
          }
        }
        utils.cpost(this, toData)
      },

      /*频道比赛报错*/
      channelErr(isclick){
        let channelName = utils.getPara('title')
        let toData = {
          url:'alert/channel',
          postData:{
            channel:channelName
          },
          callback:(res)=>{
            if(res.error_code==1){
              console.info('频道错误上报成功！')
              if(isclick){
                this.showtips = true;
                this.tipsText = '上报成功，正在修复中';
                setTimeout(()=>{
                  this.showtips = false;
                },2000)
              }

            }
          },
          error:()=>{
            console.log('网络错误');
          }
        }
        utils.cpost(this, toData)
      },
      openMLine(){
        this.mlinebox = !this.mlinebox;
        this.upArrow = !this.upArrow;
      },

      getChannelList(){
        let channelName = utils.getPara('title')
        let toData = {
          url: 'channel/list',
          callback: (res) => {

            if (res.error_code === 1) {
              // this.channelList = res.data;
              let list = res.data
              list.map((item,index)=>{
                item.active = false
                if(item.channelName==channelName){
                  this.playType = item.playType
                  this.videosrc = item.url
                  item.active = true
                }
                if(item.videoType!='iframe'){
                  this.channelList.push(item)
                }
              });
            }

          },
          error: () => {
            console.log('网络错误')
          }
        }
        utils.cpost(this, toData);
      },

      changeChannel(item){
        document.documentElement.scrollTop = document.body.scrollTop = 0;
        document.getElementsByTagName('title')[0].innerHTML = '低调看直播-'+item.channelName
        this.channelList.forEach(function (item) {
          Vue.set(item,'active',false)
        });
        Vue.set(item,'active',true);
        this.mCurrentLine = item.channelName
        this.playType = item.playType
        this.videosrc = item.url;
        if(item.playType==2){//ifarme
        }else{
          window.player.videoPause()
          this.initVideo()
//          window.player.newVideo({autoplay:true,live:true,video:item.url})
        }

      }

    },
  }



</script>



