<template>
  <div class="match-list" ref="listbox">
    <div class="content">
      <ul class="main">
        <li v-for="game in gamelist" class="item-container">
          <div v-if="game.type=='date'" class="title">
            <img src="../../../assets/images/dateIcon.png" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播"/>
            <span>{{getDateWeek(game.matchBeginTime)}}</span>
          </div>
          <div :class="game.hot=='true'?'item-match-box bold':'item-match-box'">
            <a v-if="game.liveLines.length>0" :href="'player.html?ADTAG='+isqd+'&matchId='+game.matchId+'&title='+(game.awayName?game.awayName:'低调看直播')+'&page=index'" class="firsr-a"></a>
            <a href="javascript:void(0)" class="list-item flexBox" target="_self" style="cursor: pointer;">
              <!--比赛-->
              <div class="game flexBox">
                <div class="game-name">
                  <div class="iconImg">
                    <div class="time"><span>{{getMatchDate(game.matchBeginTime)}}</span></div>
                  </div>
                  <div v-if="game.leagueName" class="name">
                    <div
                      v-if="(game.awayName&&game.awayLogo&&game.awayName!=' ')&&game.officialNum"
                      class="color-red">
                      <span v-if="game.leagueType=='足球'">竞足</span>
                      <span v-else-if="game.leagueType=='篮球'">竞篮</span>
                      <span v-else>竞彩</span>
                      {{game.officialNum}}
                    </div>
                    {{game.leagueName?game.leagueName.substring(0,10):game.leagueName}}
                  </div>
                </div>
                <div class="game-info">
                  <template v-if="game.awayName&&game.awayLogo&&game.awayName!=' '">
                    <div class="team">
                      <div class="teamInfo">
                        <img v-lazy="game.homeLogo" alt="" />
                        <span>{{game.homeName}}</span>
                        <b v-if="game.liveStatus!=0">{{game.homeGoals}}</b>
                        <div v-else class="line"> </div>
                      </div>
                      <div class="teamInfo">
                        <img v-lazy="game.awayLogo" alt="" />
                        <span>{{game.awayName}}</span>
                        <b v-if="game.liveStatus!=0">{{game.awayGoals}}</b>
                        <div v-else class="line"> </div>
                      </div>
                    </div>
                  </template>
                  <template v-else>
                    <div class="special-game">
                      <div class="show-content">
                        <img v-lazy="game.homeLogo" alt="" />
                        <div class="word">{{game.homeName}}</div>
                      </div>
                    </div>
                  </template>
                </div>
                <!--(-1:已结束，0未开始，1，即将开始(提前十分钟)，2正在直播)，-->
                <div v-if="game.liveStatus==2" class="playeIcon">直播中</div>
                <div v-if="game.liveStatus==0" class="notStart ">未开始</div>
              </div>
            </a>
            <div class="lineList">
              <div class="no-line" v-if="game.liveLines.length<=0">等待线路更新中</div>
              <div class="line-info" v-if="game.liveLines.length>0">
                <a v-for="(line,idx) in game.liveLines"
                   :key="idx"
                   :href="line.playType==3?'download.html?ADTAG='+isqd :'player.html?ADTAG='+isqd+'&currentId='+line.id+'&matchId='+line.matchId+'&title='+(game.leagueName?game.leagueName:'低调看直播')+'&page=index'" target="_self">
                  <span v-if="line.playType==3" style="color:#ff4333;">{{line.name.substring(0,7)}}</span>
                  <span v-else>{{line.name.substring(0,7)}}</span>
                </a>

              </div>
            </div>
          </div>
          <listBan :ListBan="ListBan" v-if="game.banner"></listBan>
        </li>
        <li class="loadmore" v-show="nomorelist">
          <div class="nodata" v-if="nodata"></div>
          <a v-else href="javascript:void (0)" @click="loadmore">
            <img src="../../../assets/images/loading.gif" class="loading" v-if="loadTxt=='正在加载中…'"/>
            <span>{{ loadTxt }}</span>
          </a>
        </li>
        <li v-if="nodata">
          <div class="nodata" v-if="nodata"><img src="../../../assets/images/nodata.png" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播"/></div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type='text/ecmascript-6'>
  import * as utils from '../../../common/js/utils'
  import listBan from '../listBan.vue'
  export default{
    name: 'matchList',
    props:{
      Ischannel:true,
      MatchType:Number,
      ListBan:{
        type:Object,
        defalut:{
          iosShow:false,androidShow:false
        }
      }
    },
    components:{listBan},
    data() {
      return {
        gamelist: [],
        page:{
          start:0,
          pageSize:15
        },
        nomorelist:true,
        loadTxt:'正在加载中…',
        scrollState:true,
        canload:true,
        img:{},
        nodata:false,
        time:'',

        isqd:'',

        oldTime:'',

        isMatchList:true,
      }
    },
    created() {
      this.isqd = utils.getPara('ADTAG')?utils.getPara('ADTAG'):''
    	/*this.getToday();*/
      this.initData();
      this.scrollload();
      this.$nextTick(()=>{
        let htmlFont = 100 * (document.body.clientWidth / 750)
        let htmlHeight = document.body.clientHeight
        let temp = (0.96+1.88+0.76+.6)*htmlFont
        this.$refs.listbox.style.minHeight = (htmlHeight-temp)+'px'

      })
    },
    watch:{
        MatchType:function (val,oldval) {
          this.canload = true;
          this.initData();
          if(val!=oldval){
              this.nodata = false;
          }
        }
    },
    methods: {
			getToday(){
				let toData = {
					url:'game/v2/getServerTime',
					callback:(res)=>{
						if(res.error_code==1){
							console.log(res);
							this.time = res.data.date;
						}
					}
				};
				utils.cpost(this, toData);
			},
      initData(){
        this.page= {
          start:0,
          pageSize:15,
          type : this.MatchType
        };
        this.loadTxt ='正在加载中…';
        let toData = {
          url:'game/v2/list',
          postData:this.page,
          callback:(res)=>{
            if (res.error_code === 1) {
              /*console.log(res)*/
              let list = res.data;
              if(list.length>0){
                this.oldTime = list[0].matchBeginTime
                this.gamelist = this.lineType(list);
                this.gamelist[0].type = 'date'
                if(list.length>=7){
                  this.gamelist[6].banner = true
                }
              }
              if(list.length>0&&list.length<this.page.pageSize){

                this.nomorelist = false;
                this.nodata = false;

              }else if(list.length==0){
                this.gamelist = list
                this.nomorelist = true;
                this.loadTxt = '暂时没有数据'
                this.nodata = true;
              }
            }
          },
          error:()=>{
              console.error('网络错误')
          }
        }
        utils.cpost(this, toData);
      },

      scrollload(){
        var _that = this;
        window.onscroll = function (event) {

          if(_that.isMatchList){
            var wScrollY = window.scrollY; // 当前滚动条位置
            var wInnerH = window.innerHeight; // 设备窗口的高度（不会变）
            var bScrollH = document.body.scrollHeight; // 滚动条总高度
            /*console.log('get in scrollload')*/
            if (wScrollY + wInnerH >= bScrollH-50&&_that.scrollState&&_that.canload) {
              /*console.log(wScrollY + '--' + wInnerH +'+=='+(wScrollY + wInnerH) + '--' + bScrollH)*/
              _that.scrollState = false;
              _that.loadmore();
              setTimeout(function () {
                _that.scrollState = true;
              },1000)
            }
          }
        }
      },

      loadmore(){
        this.nomorelist = true;
        if(this.canload){
          this.page.start = this.gamelist.length;
          let param = this.page;
          param.type = this.MatchType
          this.loadTxt ='正在加载中…';
          let toData = {
            url:'game/v2/list',
            postData:param,
            callback:(res) => {

              if (res.error_code === 1) {
                if(res.data.length>0){
                  let list = this.lineType(res.data);
                  this.gamelist.push(...list);
                  this.nomorelist = false;
                  if(res.data.length<15){
                    this.canload = false;
                    this.nomorelist = true;
                    this.loadTxt = '没有更多数据了';
                  }
                }else{
                  this.canload = false;
                  this.nomorelist = true;
                  this.loadTxt = '没有更多数据了';
                }
              }
            },

          }
          utils.cpost(this,toData);
        }
      },
      lineType(list){ // 筛选播放源，1,2为移动端，（0,1）为pc端
        list.map(function (item,index) {
          if(item.liveLines.length>0){
            let temp = [];
            item.liveLines.map(function (ele,idx) {
              if(ele.source==1||ele.source==2){ // 1,2为移动端可播
                temp.push(ele);
              }
              let str = (ele.name)+(ele.tag?ele.tag:'');
              ele.name = str
            });
            item.liveLines = temp;
          }
          item.type = 'macth' // match 为比赛 date 为时间
        });
        this.joinDatalist(list)
        return list
      },
      joinDatalist(list){
        // console.log(list);
        list.map((item,index)=>{
           // console.log(this.oldTime.split(' ')[0],item.matchBeginTime.split(' ')[0],this.oldTime.split(' ')[0]==item.matchBeginTime.split(' ')[0])
          if(this.oldTime.split(' ')[0]!=item.matchBeginTime.split(' ')[0]) {

            if(item.awayLogo){
              this.oldTime = item.matchBeginTime
              item.type = 'date'
            }else{
              item.type = 'macth'
            }
          }
          /*if(index== 6 || index == 13){
           item.banner = true
           }else{
           item.banner = false
           }*/
        })
      },
      getMatchDate(time){
        if(time){
          let str = time.split(' ')[1];
          let arr = str.split(':');
          arr.pop();
          time = arr.join(':');
        }
        return time
      },
      getDateWeek(time){
        let str = time
        if(time){
          let ymd = time.split(' ')[0]
          let iosd = time.replace(/-/g,'/');
          let d = (new Date(iosd)).getDay();
          let strd = ''

          switch (d){
            case 0:
              strd = '星期日';
              break;
            case 1:
              strd = '星期一';
              break;
            case 2:
              strd = '星期二';
              break;
            case 3:
              strd = '星期三';
              break;
            case 4:
              strd = '星期四';
              break;
            case 5:
              strd = '星期五';
              break;
            case 6:
              strd = '星期六';
              break;
          }

          str = ymd + ' ' + strd
        }
        return str
      }
    },
    beforeDestroy(){
      this.isMatchList = false
    }
  }
</script>
<style lang="less">
  @import "matchList.less";
</style>




