<template>
  <div :class="{timeScore:true,black:IsBlack,white:(!IsBlack),'none':!isShow}">
    <div class="team-info">
     <span>{{score.homeName?score.homeName.substring(0,7)+(score.homeName.length>7?'...':''):score.homeName}}</span>
      <div>
        <img :src="score.homeLogo?score.homeLogo:'../../../static/logo.png'" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播">
      </div>
      <div class="score" style="margin-left: .3rem;">{{score.homeGoals}}</div>
    </div>
    <div class="line"></div>

    <div class="team-info">
      <div style="margin-right:.3rem;" class="score">{{score.awayGoals}}</div>
      <div>
        <img :src="score.awayLogo?score.awayLogo:'../../../static/logo.png'" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播">
      </div>
      <span>{{score.awayName?score.awayName.substring(0,8)+(score.awayName.length>8?'...':''):score.awayName}}</span>
    </div>
  </div>
</template>
<script>
  import * as utils from '../../common/js/utils'
  export default{
    name:'timeScore',
    props:{
      IsBlack:Boolean
    },
    data(){
      return{
        score:{},
        isShow:false
      }
    },
    created(){
      this.getGoals();
      let s = 1000 * 30
      if(this.IsBlack){
        setInterval(()=>{
          this.getGoals();
        },s)
      }
    },
    methods:{
      /*获取比分信息*/
      getGoals(){
        let toData = {
          url: 'game/getGoals',
          postData:{
            matchId: utils.getPara('matchId')
          },
          callback:(res)=>{
            /*console.log(res);*/
            if(res.error_code==1){
              this.score = res.data
              if(res.data.awayLogo){
                this.score = res.data
                this.isShow = true
              }else{
                this.isShow = false
              }
            }else{
              this.isShow = false
            }
          },
          error:()=>{
            console.error('网络错误')
          }
        }
        utils.cpost(this, toData);
      }
    }
  }
</script>
<style scoped>
  .none{
    display: none!important;
  }
  .timeScore{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 1.02rem;
  }
  .timeScore.black{
    background: #000;
    color: #fff;
    font-size:.24rem;
  }
  .timeScore.white{
    background: #fff;
    color:#000;
    font-size:.24rem;
  }
  .timeScore .team-info{
    flex: 1;
    display: flex;
    align-items: center;
  }
  .timeScore .team-info:nth-of-type(1){
    justify-content: flex-end;
  }
  .timeScore img{
    width: .42rem;
    height: .42rem;
    vertical-align: middle;
  }
  .timeScore .score{
    font-size:.35rem;
    font-weight: bold;
  }
  .timeScore .line{
    width: .15rem;
    height: .04rem;
    background: #fff;
    margin: 0 .24rem;
  }
  .timeScore.white .line{
    background:#000;
  }

  .timeScore .team-info{

  }
  .timeScore .team-info span{
    vertical-align: middle;
    margin:0 .14rem ;
  }
</style>



