<template>
  <div class="listBan" v-show="isShow" @click="countClick">
    <a v-if="url" :href="url"></a>
    <img :src="img" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播">
  </div>
</template>
<script>
  import * as utils from '../../common/js/utils'
  export default{
    name:'listBan',
    props:{
      ListBan:{
        type:Object,
        defalut:{
          iosShow:false,androidShow:false
        }
      }
    },
    computed:{
      img:function () {
        let browser = utils.checkDevice()
        if(browser.versions.android){
          return this.ListBan.androidImg
        }else{
          return this.ListBan.iosImg
        }
      },
      url:function () {
        let browser = utils.checkDevice()
        if(browser.versions.android){
          return this.ListBan.androidUrl
        }else{
          return this.ListBan.iosUrl
        }
      },
      isShow:function () {
        let browser = utils.checkDevice()
        if(browser.versions.android){
          return this.ListBan.androidShow
        }else{
          return this.ListBan.iosShow
        }
      }
    },
    methods:{
      countClick(){
        let browser = utils.checkDevice()
        if(browser.versions.android){
          MtaH5.clickStat('listBanner',{'phone':'android'})
        }else{
          MtaH5.clickStat('listBanner',{'phone':'ios'})
        }
      }
    }
  }
</script>
<style>
  .listBan{
    width: 100%;
    min-height: .64rem;
    text-align: center;
    line-height: .64rem;
    position: relative;
    z-index: 200;
  }
  .listBan a{
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top:0;
    left: 0;
  }
  .listBan img{
    display: block;
    width: 100%;
    border:none;
  }
</style>



